import Vue from 'vue'
import VueRouter from "vue-router";
import Index from "../components/Index"
import List from "../components/user/List";
//安装路由
Vue.use(VueRouter)

//配置导出路由
export default new VueRouter({
  routes:[
    {
      path:'/',
      redirect:'/index'
    },
    {
      path:'/index',
      component:Index
    },
    {
      path:'/users',
      component:List
    }
    ]
});



