<template>
<div>
  <el-carousel indicator-position="outside">
    <el-carousel-item v-for="item in imags" :key="item">
      <el-image
        style="height: 300px"
        :src="item"
        fit="contain"></el-image>
    </el-carousel-item>
  </el-carousel>
</div>
</template>

<script>
import imag1 from "../assets/imags/1.jpeg";
import imag2 from "../assets/imags/2.jpeg";
import imag3 from "../assets/imags/3.jpeg";
import imag4 from "../assets/imags/4.jpeg"
export default {
  name: "Index",
  data(){
    return{
      imags:[imag1,imag2,imag3,imag4]
    }
  }
}
</script>

<style scoped>

</style>



