<template>
<div>
  <el-table
    :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%">
    <el-table-column
      label="编号"
      width="180">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.id }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="姓名"
      width="180">
      <template slot-scope="scope">
        <el-popover trigger="hover" placement="top">
          <p>姓名: {{ scope.row.name }}</p>
          <p>住址: {{ scope.row.address }}</p>
          <div slot="reference" class="name-wrapper">
            <el-tag size="medium">{{ scope.row.name }}</el-tag>
          </div>
        </el-popover>
      </template>
    </el-table-column>
    <el-table-column label="生日" prop="bir"></el-table-column>
    <el-table-column label="性别" prop="sex"></el-table-column>
    <el-table-column label="地址" prop="address"></el-table-column>
    <el-table-column align="right">
      <template slot="header" slot-scope="scope">
        <el-input
          v-model="search"
          size="mini"
          placeholder="输入姓名的关键字进行搜索"/>
      </template>
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>

          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            slot="reference">删除</el-button>

      </template>
    </el-table-column>
  </el-table>

  <el-pagination style="margin: 15px"
                 background
    layout="prev, pager, next,jumper,total,sizes"
                 @current-change="findPage"
                 @size-change="findSize"
    :total="50">
  </el-pagination>
  <el-button style="margin: 10px 0px;" @click="saveUserInfo" type="success" size="mini">添加</el-button>
  <transition name="el-zoom-in-center">
    <div v-show="show" class="transition-box">
      <el-form hide-required-asterisk :rules="rules" ref="form" :model="form" label-suffix=":" label-width="80px">
        <el-form-item label="姓名" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="生日" prop="bir">
            <el-date-picker type="date" placeholder="选择日期" v-model="form.bir" style="width: 100%;"></el-date-picker>
        </el-form-item>
        <el-form-item label="性别">
          <el-radio-group v-model="form.sex">
            <el-radio label="男"></el-radio>
            <el-radio label="女"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="详细地址" prop="address">
          <el-input type="textarea" v-model="form.address"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit()">保存用户信息</el-button>
          <el-button @click="saveUserInfo">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </transition>
</div>
</template>

<script>
export default {
  name: "List",
  data() {
    return {
      tableData: [],
      search:'',
      show:false,
      form: {
        name: '',
        bir:'',
        sex:'男',
        address:''
      },
      rules: {
        name: [
          { required: true, message: '请输入用户姓名！', trigger: 'blur' },
        ],
        bir: [
          { required: true, message: '请选择用户出生年月！', trigger: 'blur' }
        ],
        address: [
          { required: true, message: '请选择用户地址！', trigger: 'blur' }
        ],
      }
    }
  },
  methods: {
    findPage(page){
      console.log(page)
    },
    findSize(size){
      console.log(size)
    },
    saveUserInfo(){
      this.show=true;
      this.form={sex:'男'};
    },
    handleEdit(index, row) {
      console.log(index, row);
      this.show=true;
      this.form=row;
    },
    handleDelete(index, row) {
      console.log(index, row);
      //发送axios请求；
      this.$http.get("http://localhost:8080/userInfo/delete/"+row.id).then(res=>{
        if(res.data.status){
          this.$message({
            message: res.data.msg,
            type: 'success'
          });
          this.findAllTableData();
        }else {
          this.$message.error(res.data.msg);
        }
      })
    },
    onSubmit() {
      //发送一个ajax请求

      this.$http.post("http://localhost:8080/userInfo/addOrUpdate",this.form).then(res=>{
        console.log(res.data);
        if(res.data.status){
          this.$message({
            message: '恭喜你，'+res.data.msg,
            type: 'success'
          });
          this.form={sex:'男'};
          this.show=false;
          this.findAllTableData();
        }else {
          this.$message.error(res.data.msg);
        }
      });
    },
    findAllTableData(){
      this.$http.get("http://localhost:8080/userInfo/list").then(res=>{
        this.tableData=res.data;
      });
    }
  },
  created() {
    this.findAllTableData();
  }
}
</script>

<style scoped>
.transition-box {
  margin-bottom: 10px;
  width: 100%;
  height: 400px;
  border-radius: 4px;
  text-align: left;
  padding: 40px 20px;
  box-sizing: border-box;
  margin-right: 20px;
}
</style>



